* {
  box-sizing: border-box;
}

.swiper-container {
  width: 300px;
  height: 50px;
  border: 1px solid red;
}

.item {
  width: 90px;
  border: 1px solid blue;
  height: 100%;


}

.swiper-button-next {
  right: 0;
  color: red;

}

.swiper-button-next:after{
  display: none;
}
